import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HttpServiceProvider } from '../../providers/http-service/http-service';
import { LoginPage }  from '../login/login';

@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
    // 定时器
    user:any = {addr:'', password:'', spassword:''}

  	constructor(public navCtrl: NavController, public navParams: NavParams,public http: HttpServiceProvider) {
  	}


  	login(){
  		this.navCtrl.push(LoginPage)
  	}

    register(){
      if(this.user.addr.trim()=='' || this.user.password.trim()==''){
        this.http.show('地址或密码不能为空')
        return false;
      }
      if(this.user.password.trim() != this.user.spassword.trim()){
        this.http.show('两次密码不一样')
        return false;
      }
      this.http.register(this.user, (result) => {
        if(result)
          this.navCtrl.push(LoginPage)
      })
    }

  	ionViewDidLoad() {
    	console.log('ionViewDidLoad registerPage');
  	}

}



