import { Injectable } from '@angular/core';
import { LoadingController, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { HttpProvider } from '../http/http';
import 'rxjs/add/operator/map';

@Injectable()
export class HttpServiceProvider {
	loading: any;

  	constructor(public http: HttpProvider, 
  				public loadingCtrl: LoadingController, 
  				public storage: Storage,
		  		public alertCtrl: AlertController) {
    	console.log('Hello HttpServiceProvider Provider');
  	}

    // 活动是否结束
    isend(params, callback){
      this.http.post('/order/isend', params).then((result) => {
        if(result['status']==200){
          callback(result)
        }
        else{
          this.show(result['msg'])
          callback(null)
        }
      },(err)=>{

      })
    }

    // 我的地址
    me(callback){
      this.http.authGet('/user/me').then((result) => {
        if(result['status']==200){
          callback(result)
        }
        else{
          callback(null)
        }
      },(err)=>{

      })
    }

    // 活动总金额
    totalm(id, callback){
      this.http.get('/order/totalm?actid='+id).then((result) => {
        if(result['status']==200){
          callback(result)
        }
        else{
          callback(null)
        }
      },(err)=>{

      })
    }

    // 权重订单
    onlyOrder(params, callback){
      this.http.post('/order/onlyOrder', params).then((result) => {
        if(result['status']==200){
          callback(result)
        }
        else{
          callback(null)
        }
      },(err)=>{

      })
    }

    // 我的活动订单列表
    mylist(params, callback){
      this.http.authPost('/order/mylist', params).then((result) => {
        if(result['status']==200){
          callback(result)
        }
        else{
          callback(null)
        }
      },(err)=>{

      })
    }

    // 活动订单列表
    actorderlist(params, callback){
      this.http.post('/order/actlist', params).then((result) => {
        if(result['status']==200){
          callback(result)
        }
        else{
          this.show(result['msg'])
          callback(null)
        }
      },(err)=>{

      })
    }

    // 活动列表
    actlist(params, callback){
      this.http.post('/act/list', params).then((result) => {
        if(result['status']==200){
          callback(result)
        }
        else{
          this.show(result['msg'])
          callback(null)
        }
      },(err)=>{

      })
    }

    // 活动详情, id为记录主键
    actDetail(id, callback){
      this.http.get('/act/actDetail?id='+id).then((result) => {
        if(result['status']==200){
          callback(result)
        }
        else{
          this.show(result['msg'])
          callback(null)
        }
      },(err)=>{

      })
    }

  	// 登录
  	login(params, callback){
  		this.http.post('/user/signin', params).then((result) => {
  			if(result['status']==200){
  				this.storage.set('token', result['token']);
  				callback(result)
  			}
  			else{
  				this.show(result['msg'])
  				callback(null)
  			}
  		},(err)=>{

  		})
  	}

  	// 发送注册验证码
  	sendsms(params, callback){
  		this.http.post('/user/send', params).then((result) => {
  			if(result['status']==200)
  				callback(result)
  			else{
  				this.show(result['msg'])
  				callback(null)
  			}
  		},(err)=>{

  		})
  	}

  	// 注册
  	register(params, callback){
  		this.http.post('/user/register', params).then((result) => {
  			if(result['status']==200){
  				this.showLoader(result['msg'])
  				callback(result)
  			}
  			else{
  				this.show(result['msg'])
  				callback(null)
  			}
  		},(err)=>{

  		})
  	}

  	// 忘记密码
  	forget(params, callback){
  		this.http.post('/user/forget', params).then((result) => {
  			if(result['status']==200){
  				this.showLoader(result['msg'])
  				callback(result)
  			}
  			else{
  				this.show(result['msg'])
  				callback(null)
  			}
  		},(err)=>{

  		})
  	}

  	show(text) {
      	let alert = this.alertCtrl.create({
        	subTitle: text,
        	buttons: ['确定']
     	});
      	alert.present(prompt);
  	}

  	showLoader(content){
	    this.loading = this.loadingCtrl.create({
	        content: content
	    });
	    this.loading.present();
	    setTimeout(() => {
		    this.loading.dismiss();
		}, 2000);
  	}




}



